	module.exports = {"data":{"markdownRemark":{"html":"<p>Strict equality (<code>===</code>) is the counterpart to the equality operator (<code>==</code>). Unlike the equality operator, strict equality tests both the <code>type</code> and <code>value</code> of the compared elements.</p>\n<h2>Examples</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>3 === 3   // true\n3 === '3' // false</code></pre>\n      </div>\n<p><em>In the second example, <code>3</code> is a <code>Number</code> type and <code>'3'</code> is a <code>String</code> type.</em></p>","fields":{"slug":"/certificates/comparison-with-the-strict-equality-operator/"},"frontmatter":{"title":"Comparison with the Strict Equality Operator"}}},"pathContext":{"slug":"/certificates/comparison-with-the-strict-equality-operator/"}}

